<template>
  <div id="app">
    <header>
      <div class="headerIn">
        <router-link to="/">
          <img src="../static/logo.png" alt="this is logo">
        </router-link>
        <ul class="headerR right">
          <li v-for="item in navs" target="_blank" :key="item.key" @mouseover="handlLinkHover(item.key)" @mouseleave="handlLinkLeave(item.key)" :class="item.key == tab ? 'active' : ''">
            <div class="line1"></div>
            <div class="line2"></div>
            <div class="line3"></div>
            <div class="line4"></div>
            <router-link :to="item.path">{{ item.name }}</router-link>
          </li>
        </ul>
      </div>
    </header>
    <router-view/>
    <footer>
      <p>作者：关贺</p>
      <div>
        <a v-for="item in lianxi" :key="item.name" @mouseover="hover($event.currentTarget, item.id)" @mouseleave="leave($event.currentTarget, item.id)" :style="item.imgPath" :href="item.path"></a>
      </div>
      <p>vue开源作品不定期更新</p>
    </footer>
  </div>
</template>

<script>
export default {
  name: 'app',
  data () {
    return {
      navs: [{
        name: '打砖块',
        path: '/about',
        key: 1
      }, {
        name: '作品展示',
        path: '/opus',
        key: 2
      }, {
        name: '个人资料',
        path: '/personalData',
        key: 3
      }, {
        name: '相关链接',
        path: '/relatedLinks',
        key: 4
      }, {
        name: '回忆录',
        path: '/memoirs',
        key: 5
      }],
      tab: 0,
      lianxi: [{
        name: 'qq',
        path: 'http://wpa.qq.com/msgrd?v=3&uin=2424173338&site=qq&menu=yes',
        imgPath: {'background': 'url(../static/icon32.png) no-repeat -64px -160px'},
        hoverImgPath: {'background': 'url(../static/icon32.png) no-repeat -64px -64px'},
        id: 0
      }, {
        name: 'facebook',
        path: '#',
        imgPath: {'background': 'url(../static/icon32.png) no-repeat -128px -160px'},
        hoverImgPath: {'background': 'url(../static/icon32.png) no-repeat -128px -64px'},
        id: 1
      }, {
        name: 'weixin',
        path: '#',
        imgPath: {'background': 'url(../static/icon32.png) no-repeat 0 -160px'},
        hoverImgPath: {'background': 'url(../static/icon32.png) no-repeat 0 -64px'},
        id: 2
      }, {
        name: 'email',
        path: 'mailto:2424173338@qq.com',
        imgPath: {'background': 'url(../static/icon32.png) no-repeat -640px -160px'},
        hoverImgPath: {'background': 'url(../static/icon32.png) no-repeat -640px -64px'},
        id: 3
      }, {
        name: 'phone',
        path: 'tel:18943541649',
        imgPath: {'background': 'url(../static/icon32.png) no-repeat -672px -160px'},
        hoverImgPath: {'background': 'url(../static/icon32.png) no-repeat -672px -64px'},
        id: 4
      }]
    }
  },
  methods: {
    handlLinkHover (e) {
      this.tab = e
    },
    handlLinkLeave () {
      this.tab = 0
    },
    hover (e, id) {
      e.style.background = this.lianxi[id].hoverImgPath.background
    },
    leave (e, id) {
      e.style.background = this.lianxi[id].imgPath.background
    }
  }
}
</script>

<style scoped>
header{
  background-color: grey;
  height: 80px;
  line-height: 80px;
  color: white;
}
.headerIn{
  width: 1200px;
  height: 80px;
  box-sizing: border-box;
  margin: 0 auto;
}
.headerIn img{
  width: 180px;
  height: 60px;
  margin: 10px;
  /* animation: xuanzhuan 4s infinite linear; */
}
.headerR{
  display: flex;
  width:500px;
  justify-content: space-between;
  margin-right: 10px;
}
.headerR li{
  width: 18%;
  text-align: center;
  height:50px;
  line-height:50px;
  margin-top: 15px;
  position: relative;
}
.headerR li div{
  position: absolute;
  background-color: white;
  transition: all 2s;
}
.headerR a{
  display: block;
  width:100%;
  height:50px;
  line-height:50px;
  box-sizing: border-box;
}
.headerR a:hover{
  color: orange;
  background: rgba(0,0,0,0.3);
}
.line1, .line2{
  top: 0;
}
.line2, .line3{
  right: 0;
}
.line3, .line4{
  bottom: 0;
}
.line4, .line1{
  left: 0;
}
.line1, .line3{
  height: 2px;
  width: 0;
}
.line2, .line4{
  height: 0;
  width: 2px;
}
.active .line1, .active .line3{
  width: 100%;
  /* animation: huan 2s; */
}
.active .line2, .active .line4{
  height: 100%;
  /* animation: huan1 2s; */
}
@keyframes huan {
  from{width: 0}
  to{width: 100%}
}
@keyframes huan1 {
  from{height: 0}
  to{height: 100%}
}
@keyframes xuanzhuan {
  from{transform:rotate(0)}
  to{transform:rotate(360deg)}
}
#app {
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #2c3e50;
}
footer{
  padding: 20px;
  background-color: grey;
  text-align: center;
  color: white;
}
footer div{
  margin: 20px 0;
  text-align: center;
}
footer div a{
  display: inline-block;
  width: 32px;
  height: 32px;
  margin-left: 10px;
  background: url(./assets/icon32.png) no-repeat -64px -160px;
}
footer div a:first-child{
  margin-left: 0;
}
@media screen and (max-width: 768px) {
  header{height: 120px;line-height: 60px;font-size: 14px;}
  header{font-size: 14px;}
  .headerIn img{height: 50px;display:block;margin: 0 auto}
  .headerIn{width: 100%;height: 50px;padding: 10px 0;}
  .headerR li{height: 30px;line-height: 30px;}
  .headerR{width: 100%;font-size: 12px;margin-right: 0;padding-bottom: 10px}
  /* .headerIn img{width: 40px;height: 40px;} */
  .headerR a{height: 30px;line-height: 30px;}
}
</style>



