<template>
  <div class="hello">
    <el-carousel :interval="4000" :type="pingmu ? 'card' : ''" :height="changHeight">
      <el-carousel-item v-for="item in banner" :key="item.key">
        <img :src="item.path" alt="">
      </el-carousel-item>
    </el-carousel>
    <h3>项目展示</h3>
    <div class="opusContent">
      <a v-for="items in opus" :key="items.id" class="imgContent" :class="items.id % 3 !== 1 ? 'imgContent1' : ''" :href="items.path" target="_blank">
        <div>
          <img :src="items.imgUrl" alt="">
        </div>
        <div>
          <p>项目名称：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ items.name }}</p>
          <p>项目周期：&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;{{ items.date }}</p>
          <p>项目所属公司：&nbsp;&nbsp;{{ items.company }}</p>
        </div>
      </a>
    </div>
    <!-- <sanjiliandong :sheng="sheng" @shengChange="shengClick" @shiChange="shiClick" @change="xuanzhong"/> -->
  </div>
</template>

<script>
// import sanjiliandong from './sanliandong'
export default {
  name: 'HelloWorld',
  data () {
    return {
      pingmu: true,
      changHeight: '450px',
      sheng: [{
        id: 1,
        name: '北京',
        children: [{
          id: 2,
          name: '北京市',
          children: [{
            id: 3,
            name: '朝阳区'
          }]
        }]
      }, {
        id: 1,
        name: '吉林',
        children: [{
          id: 2,
          name: '吉林市',
          children: [{
            id: 3,
            name: '丰满区'
          }]
        }]
      }, {
        id: 1,
        name: '黑龙江',
        children: [{
          id: 2,
          name: '黑龙江市',
          children: [{
            id: 3,
            name: '五峰'
          }]
        }]
      }, {
        id: 1,
        name: '上海',
        children: [{
          id: 2,
          name: '上海市',
          children: [{
            id: 3,
            name: '浦东'
          }]
        }]
      }],
      banner: [{
        path: '../../static/books3b2.jpg',
        key: 1
      }, {
        path: '../../static/bridge3b2.jpg',
        key: 2
      }, {
        path: '../../static/keji.jpg',
        key: 3
      }],
      a: 400,
      opus: [{
        path: 'http://www.wangdoukeji.com/',
        imgUrl: '../../static/wdkj.jpg',
        name: '网豆科技官网',
        date: '不定期维护',
        company: '北京网豆科技',
        id: 1
      }, {
        path: 'http://www.prcfe.com/finance/2017/1117/209229.html',
        imgUrl: '../../static/mssl.jpg',
        name: '觅食森林餐饮系统',
        date: '2017-8至2017-11',
        company: '北京西单觅食森林',
        id: 2
      }, {
        path: 'https://www.pfmfinance.com',
        imgUrl: '../../static/pfmt.jpg',
        name: '品富美投PC',
        date: '2017-8至2017-8',
        company: '品富美投',
        id: 3
      }, {
        path: 'http://39.108.14.60',
        imgUrl: '../../static/lmmb.jpeg',
        name: '罗玛漫步商品展示',
        date: '2017-12至2017-12',
        company: '北京罗玛漫步',
        id: 4
      }, {
        path: 'https://www.doudot.cn/account/login/',
        imgUrl: '../../static/ddcy.jpg',
        name: '豆点餐饮商户管理',
        date: '不定期维护',
        company: '北京网豆科技',
        id: 5
      }, {
        path: 'http://shop.wangdoukeji.com/',
        imgUrl: '../../static/wdsc.jpg',
        name: '网豆商城小程序版',
        date: '2017-12至2017-12',
        company: '北京网豆科技',
        id: 6
      }]
    }
  },
  // components: { sanjiliandong },
  mounted () {
    // window.onresize = function () {
    //   var a = document.getElementsByClassName('el-carousel__item')[0].offsetWidth
    //   for (var i in document.getElementsByClassName('el-carousel__item')) {
    //     document.getElementsByClassName('el-carousel__item')[i].style.height = a * 2 / 3 + 'px'
    //   }
    // }
    if (document.body.clientWidth < 768) {
      this.changHeight = '280px'
      this.pingmu = false
    }
  },
  methods: {
    shengClick (e, i) {
      console.log(i)
    },
    shiClick (e, i) {
      console.log(i)
    },
    xuanzhong (e) {
      console.log(e)
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.el-carousel{
  padding-top:40px;
  background: black;
}
.el-carousel__container{
  background: black;
}
.el-carousel__item{
  overflow: hidden;
}
.el-carousel__item img{
  position: relative;
  width: 100%;
  transition: all 2s;
}
.is-active img{
  transform: scale(1.1);
}
.el-carousel__item:nth-child(3) {
  background-color: black;
}
.el-carousel__item .zhuan{
  animation: xuan 20s infinite linear;
  width:66.6%;
  left: 50%;
  margin-left:-33.3%;
  border-radius: 100%; 
  transform: scale(1.5);
}
.hello{background-color: black;}
.hello>h3{font-weight: 100;line-height: 80px;font-size: 28px;letter-spacing: 4px;margin: 20px 0 10px 0;background-color: #222;text-align:center;color:white;}
.opusContent{width: 1200px;margin: 0 auto;padding-bottom: 20px;}
.imgContent{display: inline-block;width: 380px;margin-top: 30px;}
.imgContent div:nth-child(1){
  width: 380px;
  height: 200px;
  overflow: hidden;
}
.imgContent div:nth-child(1) img{
  transition: all 2s;
  width:100%;
  min-height: 100%;
}
.imgContent{position: relative;transition: all .8s;}
.imgContent:hover{transform: translate(0,-10px);box-shadow: 0 0 16px 4px rgba(255, 255, 255, 0.6);}
.imgContent:hover p{color: orange;}
.imgContent div:nth-child(1) img:hover{transform:scale(1.2);}
.imgContent div:nth-child(2){background-color: #222;padding:5px 0;}
.imgContent div:nth-child(2) p{white-space: nowrap;overflow: hidden;text-overflow: ellipsis;padding: 2px 20px;}
.imgContent1{margin-left: 30px;}
@keyframes xuan {
  from{transform:rotate(360deg)}
  to{transform: rotate(0)}
}
@media screen and (max-width: 768px) {
  .el-carousel{padding-top: 20px;}
  .el-carousel__item, .el-carousel__mask{height: initial;}
  .hello>h3{line-height: 47px;font-size: 17px;}
  .opusContent{width: 100%;margin: 0 auto;}
  .imgContent1{margin-left: 0}
  .imgContent{width: 90%;margin-left: 5%;}
  .imgContent div:nth-child(1){width: 100%;}
}
</style>



