<template>
  <div class="lianjieqiang">
    <div class="leftTotalContent">
      <div class="leftContent">
        <div v-for="(item, index) in fengxiangbiao" :key="item.id" :class="index % 2 === 0 ? 'leftCircle' : 'leftRectangle'" @mouseover="changIndex(index)">
          <div :class="index === fengxiangbiaoHover ? 'indexHover' : ''"><span>{{ index % 2 === 0 ? '' : item.name}}</span><div class="huandongbeijing"></div></div>
        </div>
      </div>
      <div class="rightContent">
        <div v-for="(item, index) in fengxiangbiao" :key="item.id" :class="index % 2 === 1 ? 'rightCircle' : 'rightRectangle'" @mouseover="changIndex(index)">
          <div :class="index === fengxiangbiaoHover ? 'indexHover' : ''"><span>{{ index % 2 === 1 ? '' : item.name}}</span><div class="huandongbeijing"></div></div>
        </div>
      </div>
    </div>
    <!-- <div class="introduceContent">

    </div> -->
    <div class="imgContent">
      <div v-for="(item, index) in fengxiangbiao" :key="index" v-if="index === fengxiangbiaoHover" :class="item.className">
        <img :src="item.imgUrl" alt="">
      </div>
    </div>
    <div class="smallImgContent">
      <div v-for="(item, index) in fengxiangbiao" :key="index" v-if="index === smallImgShow">
        <div class="firstImg bounceInRight">
          <img :src="item.smallImgUrlOne" alt="">
        </div>
        <div class="secondImg bounceInRight">
          <img :src="item.smallImgUrlTwo" alt="">
        </div>          
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data () {
    return {
      fengxiangbiaoHover: 0,
      smallImgShow: -1,
      fengxiangbiao: [{
        id: 1,
        name: '我的童年',
        imgUrl: '../../static/keji.jpg',
        smallImgUrlOne: '../../static/keji.jpg',
        smallImgUrlTwo: '../../static/keji.jpg',
        className: 'animated bounceInDown'
      }, {
        id: 2,
        name: '我的初中',
        imgUrl: '../../static/keji.jpg',
        smallImgUrlOne: '../../static/keji.jpg',
        smallImgUrlTwo: '../../static/keji.jpg',
        className: 'animated bounceIn'
      }, {
        id: 3,
        name: '我的高中',
        imgUrl: '../../static/keji.jpg',
        smallImgUrlOne: '../../static/keji.jpg',
        smallImgUrlTwo: '../../static/keji.jpg',
        className: 'animated bounceInRight'
      }, {
        id: 4,
        name: '我的大学',
        imgUrl: '../../static/keji.jpg',
        smallImgUrlOne: '../../static/keji.jpg',
        smallImgUrlTwo: '../../static/keji.jpg',
        className: 'animated rubberBand'
      }, {
        id: 5,
        name: '我的第一份工作',
        imgUrl: '../../static/keji.jpg',
        smallImgUrlOne: '../../static/keji.jpg',
        smallImgUrlTwo: '../../static/keji.jpg',
        className: 'animated bounceInUp'
      }, {
        id: 6,
        name: '我的现在的追求',
        imgUrl: '../../static/keji.jpg',
        smallImgUrlOne: '../../static/keji.jpg',
        smallImgUrlTwo: '../../static/keji.jpg',
        className: 'animated swing'
      }]
    }
  },
  mounted () {
    var that = this
    setTimeout(function () {
      that.smallImgShow = 0
    }, 200)
  },
  methods: {
    changIndex (i) {
      var that = this
      this.fengxiangbiaoHover = i
      setTimeout(function () {
        that.smallImgShow = i
      }, 200)
    }
  }
}
</script>

<style scoped>
.lianjieqiang{overflow:hidden;background:black;width: 100%;color: white;display: flex;}
.lianjieqiang span{z-index: 999999;position: absolute;left: 50%;transform: translateX(-50%)}
.leftTotalContent{width: 40%;box-sizing: border-box;display: flex;}
.leftContent, .rightContent{width: 50%;padding-bottom: 30px;}
.leftContent{text-align: right;border-right: 1px solid white;}
.leftCircle, .rightCircle{position: relative;background-color: #f60;margin-top: 30px;width: 60px;vertical-align: bottom;height: 40px;border: 1px solid white;display: inline-block;}
.leftContent>div, .rightContent>div{line-height: 40px;height: 40px;cursor: pointer;}
.leftCircle>div, .rightCircle>div{position: relative;left: 50%;top: 50%;border-radius: 50%;border: 1px solid white;transform: translate(-50%, -50%);width: 10px;height: 10px;background-color: black;}
.leftRectangle, .rightRectangle{margin-top: 30px;text-align: center;border: 1px solid white;}
.leftCircle, .leftRectangle{border-right: none;border-top-left-radius: 4px;border-bottom-left-radius: 4px;}
.rightCircle, .rightRectangle{border-left: none;border-top-right-radius: 4px;border-bottom-right-radius: 4px;}
.huandongbeijing{width: 0;height: 40px;position: absolute;top: 0;z-index: 1;transition: all .8s;background-color: #ccc;}
.leftRectangle .huandongbeijing{right: 0;}
.rightRectangle .huandongbeijing{left: 0;}
.indexHover{position: relative;}
.indexHover span{color: #f60;}
.leftRectangle>div, .rightRectangle>div{position: relative;}
.rightRectangle .indexHover .huandongbeijing, .leftRectangle .indexHover .huandongbeijing{width: 100%;color: black;}
.imgContent{width: 40%;box-sizing: border-box;}
.imgContent div{border-radius: 4px;width: 90%;height: 440px;margin:21px auto;box-sizing: border-box;padding:5px;background: #ccc;border:1px solid white;}
.imgContent img{width:100%;height: 100%;border-radius: 4px;border: 1px solid white;}
.smallImgContent{width: 20%;box-sizing: border-box;}
.smallImgContent img{display: block;width: 100%;border-radius: 4px;height: 100%;}
.smallImgContent>div{width: 100%;box-sizing: border-box;height: 100%;padding: 21px 10px 21px 0;display: flex;flex-flow: row wrap;align-content:space-between;}
.smallImgContent>div>div{height: 45%;padding: 5px;background-color: #ccc;border-radius: 4px;}
.firstImg{animation-duration: 1s;animation-fill-mode: both;}
.secondImg{animation-duration: 2s;animation-fill-mode: both;}
</style>


